.Container {
  max-width: 1600px;
  margin: 0 auto;
  text-align: left;
}

@media (min-width: 767px) {
  .Container {
    width: 760px;
  }
}

@media (min-width: 940px) {
  .Container {
    width: 940px;
  }
}

@media (min-width: 1024px) {
  .Container {
    width: 1024px;
  }
}
